
import moment from 'moment'
import {_} from '@/libs/util'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import MyCanlendar from 'c%/mycanlendar/MyCanlendar'
import leaveModal from './schedule/leave-modal.vue'
import Tag from 'c%/Tag.vue'

import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default{
mixins: [common,globals],
components:{
Tag,
Card,
Divider,
Flexbox,
LoadMore,
leaveModal,
FlexboxItem,
MyCanlendar
},
data () {
return {
data: [],
events: [],
length: 5,
config: {
student_leave:{
enable: 1,
minutes_before: 1
}
},
current_day: this.$route.query.datetime || moment(new Date()).format("YYYY-MM-DD"),
}
},
watch: {
'current_day': function (val) {
this.init_data()
}
},
mounted () {
this.pageIndex = 0
this.getEvents()
this.loadMore()
this.getConfig()
},
methods: {
getConfig () {
this.$rest('/api/configs?cfg_name=params').get()
.success(response=>{
this.config = response.cfg_value
})
},
prepareDetail (item) {
let cp_id = item.course_prepare.cp_id
this.$router.push({path:'./prepare/'+cp_id,params: {id:cp_id}})
},
showLeaveBtn (row) {
let student_leave = this.config.student_leave||{},
enable = _.isUndefined(student_leave.enable)?true:student_leave.enable,
minutes_before = student_leave.minutes_before||1440,
allowDay = moment(new Date()).add(minutes_before,'minutes').format('YYYY-MM-DD HH:mm'),
courseDay = this.$filter('int_date')(row.int_day)+' '+this.$filter('int_hour')(row.int_start_hour)


if(enable){
if(moment(allowDay).isBefore(courseDay)&&row.is_attendance===0&&row.student_leave==null&&row.allow_leave == 1){
return true
}
}
return false
},
init_data () {
this.loading = true
this.dr().getData(this.data_uri)
.then(data=>{
this.data = data.list
setTimeout(()=>{
this.loading = false
},500)
})
},
refreshData () {
this.loading = true
this.dr().refreshData(this.data_uri)
.then(data=>{
this.data = data.list
setTimeout(()=>{
this.loading = false
},500)
})
},
leave (row) {
this.$refs['leave']
.set('info',row)
.show()
},
getEvents () {
this.$rest('course_arranges/get_course_day?pagesize=1000')
.get()
.success(data=>{
if(data.list.length>0){
data.list.forEach(d=>{
this.events.push(this.$filter('int_date')(d))
})
}
})
}
},
computed: {
currentDay () {
let result = moment(this.current_day).format('YYYYMMDD')
return result
},
data_uri () {

return util.sprintf('course_arranges?int_day=%s&with=course_prepare',this.currentDay)
}
}
}



